import React, { useState, useEffect } from "react";
import * as XLSX from "xlsx";
import { Document, Packer, Paragraph, TextRun } from 'docx';
import { saveAs } from 'file-saver';
import "./SearchPage.css"; // App.css SearchPage.css   .
import logo from './assets/logo.png'; 

const API_BASE = process.env.REACT_APP_API_BASE_URL || "http://localhost:8000";

interface ChunkPreview {
  file: string;
  page: number;
  preview: string;
}
interface HistoryItem {
  question: string;
  answer: string;
  file: string;
  bookmark: boolean;
  previewChunks: ChunkPreview[];
}

function getSortedHistory(history: HistoryItem[]) {
  const bookmarks = history.filter((h) => h.bookmark);
  const others = history.filter((h) => !h.bookmark);
  return [...bookmarks, ...others];
}

const SearchPage: React.FC = () => {
  const [question, setQuestion] = useState("");
  const [language, setLanguage] = useState<"ko" | "en">("ko");
  const [contracts, setContracts] = useState<string[]>([]);
  const [file, setFile] = useState<string>(" ");
  const [loading, setLoading] = useState(false);
  const [history, setHistory] = useState<HistoryItem[]>([]);
  const [selectedIdx, setSelectedIdx] = useState<number | null>(null);

  // ---     ---
  const [selectedIndices, setSelectedIndices] = useState<number[]>([]);
  const [currentView, setCurrentView] = useState<'history' | 'bookmarks'>('history');
  const [isDownloadMenuOpen, setIsDownloadMenuOpen] = useState(false);

  // ---      ---
  const handleViewChange = (view: 'history' | 'bookmarks') => {
    setCurrentView(view);
    setSelectedIdx(null);
    setSelectedIndices([]);
  };

  const handleSelectionChange = (indexToToggle: number) => {
    setSelectedIndices(prev =>
      prev.includes(indexToToggle)
        ? prev.filter(index => index !== indexToToggle)
        : [...prev, indexToToggle]
    );
  };

  const handleDownload = (format: 'xlsx' | 'docx') => {
    if (selectedIndices.length === 0) {
      alert("    .");
      return;
    }
    const selectedData = selectedIndices.map(index => sortedHistory[index]);
    const today = new Date().toISOString().slice(0, 10);

    if (format === 'xlsx') {
      const excelData = selectedData.map(item => ({
        '': item.question, 'AI ': item.answer, ' ': item.file, ' ': item.bookmark ? 'Y' : 'N'
      }));
      const worksheet = XLSX.utils.json_to_sheet(excelData);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "");
      XLSX.writeFile(workbook, `aSK_selected_history_${today}.xlsx`);
    }

    if (format === 'docx') {
      const docChildren: Paragraph[] = selectedData.flatMap(item => [
        new Paragraph({ children: [new TextRun({ text: "[]", bold: true, size: 28 })], spacing: { before: 200, after: 100 } }),
        new Paragraph({ text: item.question, spacing: { after: 300 } }),
        new Paragraph({ children: [new TextRun({ text: "[AI ]", bold: true, size: 28 })], spacing: { after: 100 } }),
        new Paragraph({ text: item.answer, spacing: { after: 300 } }),
        new Paragraph({ children: [new TextRun({ text: `(: ${item.file})`, color: "888888", size: 20 })], spacing: { after: 200 } }),
        new Paragraph({ text: "---", alignment: 'center', spacing: { after: 400, before: 400 } })
      ]);
      const doc = new Document({ sections: [{ children: docChildren }] });
      Packer.toBlob(doc).then(blob => saveAs(blob, `aSK_selected_history_${today}.docx`));
    }
    setIsDownloadMenuOpen(false);
  };

  //  
  useEffect(() => {
    fetch(`${API_BASE}/api/contracts`)
      .then((res) => res.json())
      .then((data) => setContracts([" ", ...(data.contracts || [])]))
      .catch(() => setContracts([" "]));
  }, []);

  //  
  const handleSearch = async () => {
    if (!question.trim()) {
      alert(" .");
      return;
    }
    setLoading(true);
    setSelectedIndices([]);
    try {
      const res = await fetch(`${API_BASE}/api/search`, {
        method: "POST", headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ query: question, file_name: file === " " ? null : file, answer_lang: language }),
      });
      const data = await res.json();
      const item: HistoryItem = {
        question, answer: data.answer, file: data.file, bookmark: false,
        previewChunks: (data.preview_chunks || []).map((p: string) => ({ file: data.file, page: 0, preview: p })),
      };
      setHistory((h) => getSortedHistory([item, ...h]).slice(0, 30));
      setSelectedIdx(0);
      setQuestion("");
      setCurrentView('history');
    } catch (e) {
      alert("   .");
    } finally {
      setLoading(false);
    }
  };

  const toggleBookmark = (idx: number) => {
    setHistory((h) => getSortedHistory(h.map((it, i) => i === idx ? { ...it, bookmark: !it.bookmark } : it)));
    setSelectedIdx(0);
  };

  const deleteHistory = (idx: number) => {
    setHistory((h) => getSortedHistory(h.filter((_, i) => i !== idx)));
    setSelectedIndices(prev => prev.filter(i => i !== idx));
    setSelectedIdx(null);
  };

  const handleSelectHistory = (idx: number) => setSelectedIdx(idx);

  const sortedHistory = getSortedHistory(history);

  return (
    <div className="app">
      {/* Sidebar */}
      <aside className="sidebar">
        <div className="sidebar-header">
          <div className="sidebar-view-selector">
            <button className={`view-tab-button ${currentView === 'history' ? 'active' : ''}`} onClick={() => handleViewChange('history')}>
               
            </button>
            <button className={`view-tab-button ${currentView === 'bookmarks' ? 'active' : ''}`} onClick={() => handleViewChange('bookmarks')}>
              
            </button>
          </div>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <div className="h1" style={{ fontSize: 18, margin: 0 }}></div>
            <div className="download-container">
              <button onClick={() => setIsDownloadMenuOpen(prev => !prev)} className="download-button">
                
              </button>
              {isDownloadMenuOpen && (
                <div className="download-dropdown">
                  <button className="download-option-button" onClick={() => handleDownload('docx')}>Word (.docx) </button>
                  <button className="download-option-button" onClick={() => handleDownload('xlsx')}>Excel (.xlsx) </button>
                </div>
              )}
            </div>
          </div>
        </div>

        {sortedHistory.filter(item => currentView === 'history' || (currentView === 'bookmarks' && item.bookmark)).length === 0 ? (
          <div className="sub" style={{ textAlign: 'center', marginTop: 20 }}>
            {currentView === 'history' ? '  ' : ' '}
          </div>
        ) : (
          sortedHistory.map((item, idx) => {
            if (currentView === 'bookmarks' && !item.bookmark) return null;
            return (
              <div key={idx} className={`history-item ${selectedIdx === idx ? "active" : ""}`}>
                <input
                  type="checkbox"
                  className="history-item-checkbox"
                  checked={selectedIndices.includes(idx)}
                  onChange={() => handleSelectionChange(idx)}
                />
                <button className="history-title" onClick={() => handleSelectHistory(idx)} title={item.question}>
                  {item.question.length > 18 ? item.question.slice(0, 18) + "..." : item.question}
                </button>
                <button className={`star ${item.bookmark ? "on" : ""}`} onClick={() => toggleBookmark(idx)} title=""></button>
                <button className="del" onClick={() => deleteHistory(idx)} title=""></button>
              </div>
            );
          })
        )}
      </aside>

      {/* Main */}
      <main className="main">
        <div className="container">
          <div className="header">
            <img src={logo} alt="aSK logo" className="brand-logo" />
            <div>
              <h1 className="brand-title">    </h1>
              <div className="brand-sub">   ,    AI .</div>
            </div>
          </div>

          <form className="search-surface" onSubmit={(e) => { e.preventDefault(); handleSearch(); }}>
            <div className="toolbar">
              <label><span className="label"></span><select className="select" value={language} onChange={(e) => setLanguage(e.target.value as "ko" | "en")}><option value="ko"></option><option value="en">English</option></select></label>
              <label><span className="label"></span><select className="select" value={file} onChange={(e) => setFile(e.target.value)}>{contracts.map((opt) => (<option key={opt} value={opt}>{opt}</option>))}</select></label>
              <input className="input" type="text" placeholder={language === "ko" ? ":    ?" : "e.g., What are the termination conditions?"} value={question} onChange={(e) => setQuestion(e.target.value)} disabled={loading} />
              <button type="submit" className="btn-primary" disabled={loading}>{loading ? " ..." : " "}</button>
            </div>
          </form>

          {selectedIdx !== null && sortedHistory[selectedIdx] ? (
            <section style={{ marginTop: 10 }}>
              <div className="section-title"><span className="chip chip-lg"> </span>{sortedHistory[selectedIdx].file}</div>
              <div className="section-title"><span className="chip chip-lg"></span></div>
              <div className="card">{sortedHistory[selectedIdx].question}</div>
              <div className="section-title"><span className="chip chip-lg">AI </span></div>
              <div className="card">
                <div className="card-head"><div className="sub">     </div>
                  <button className="icon-btn" onClick={() => navigator.clipboard.writeText(sortedHistory[selectedIdx].answer)}> </button>
                </div>
                <div className="answer">{sortedHistory[selectedIdx].answer}</div>
              </div>
              <div className="section-title"><span className="chip chip-lg">  </span></div>
              <div className="card">
                {sortedHistory[selectedIdx].previewChunks.length === 0 ? (<div className="empty">  .</div>) : (<ul className="list">{sortedHistory[selectedIdx].previewChunks.map((c, i) => (<li key={i}>{c.preview}</li>))}</ul>)}
              </div>
            </section>
          ) : (
            <div className="empty" style={{ marginTop: 12 }}> /     <b className="accent"> </b> .</div>
          )}
        </div>
      </main>
    </div>
  );
};

//   SearchPage 
const SearchPageWrapper: React.FC = () => <SearchPage />;

export default SearchPageWrapper;